import BaseConfig from "../BaseConfig";
export interface IServerListVO {
  id: number;
  platform: number;
  name: string;
  ip: string;
  opentime: string;
  opStage: number;
  default: number;
  port: number;
  security: boolean;
  path: string;
  group: number;
}
export interface IConfVO {
  mod: string;
  key: string;
  value: any;
}
export interface IHonorVO {
  id: number;
  minStar: number;
  name: string;
}
export interface IShareVO {
  id: number;
  scene: number;
  title: string;
  img: string;
}
export interface IGoodsVO {
  id: number;
  name: string;
  type: number;
  subType: number;
  showIcon: string;
  titleImg: string;
}
export interface IExchangeVO {
  id: number;
  score: number;
  goodsId: number;
  goodsNum: number;
  active: number;
  showOrder: number;
  periodCount: number;
  showImg: string;
  titleImg: string;
  desc: string;
}
export interface ITurntableVO {
  id: number;
  prob: number;
  goodsId: number;
  goodsNum: number;
  showImg: string;
  desc: string;
}
export interface ITaskVO {
  id: number;
  type: number;
  progress: number;
  goods: any;
  wealth: any;
  desc: string;
  linkTo: string;
}
export interface IChatFaceVO {
  id: number;
  img: string;
}
export interface IMaleNameVO {
  id: number;
  name: string;
}
export interface IFemaleNameVO {
  id: number;
  name: string;
}
export interface ISurnameVO {
  id: number;
  name: string;
}
export interface IHeadVO {
  id: number;
  img: string;
}
export interface INameVO {
  id: number;
  name: string;
  sex: string;
}

export let ServerList: BaseConfig<IServerListVO> = new BaseConfig<IServerListVO>("ServerList");
export let Conf: BaseConfig<IConfVO> = new BaseConfig<IConfVO>("Conf");
export let Honor: BaseConfig<IHonorVO> = new BaseConfig<IHonorVO>("Honor");
export let Share: BaseConfig<IShareVO> = new BaseConfig<IShareVO>("Share");
export let Goods: BaseConfig<IGoodsVO> = new BaseConfig<IGoodsVO>("Goods");
export let Exchange: BaseConfig<IExchangeVO> = new BaseConfig<IExchangeVO>("Exchange");
export let Turntable: BaseConfig<ITurntableVO> = new BaseConfig<ITurntableVO>("Turntable");
export let Task: BaseConfig<ITaskVO> = new BaseConfig<ITaskVO>("Task");
export let ChatFace: BaseConfig<IChatFaceVO> = new BaseConfig<IChatFaceVO>("ChatFace");
export let MaleName: BaseConfig<IMaleNameVO> = new BaseConfig<IMaleNameVO>("MaleName");
export let FemaleName: BaseConfig<IFemaleNameVO> = new BaseConfig<IFemaleNameVO>("FemaleName");
export let Surname: BaseConfig<ISurnameVO> = new BaseConfig<ISurnameVO>("Surname");
export let Head: BaseConfig<IHeadVO> = new BaseConfig<IHeadVO>("Head");
export let Name: BaseConfig<INameVO> = new BaseConfig<INameVO>("Name");
